
import React from 'react';

interface FilterOption {
  key: string;
  label: string;
  icon: React.ComponentType<{ className?: string }>;
}

interface MobileFilterButtonsProps {
  options: FilterOption[];
  activeFilter: string;
  onFilterChange: (filter: string) => void;
  className?: string;
}

export const MobileFilterButtons = ({ 
  options, 
  activeFilter, 
  onFilterChange, 
  className = '' 
}: MobileFilterButtonsProps) => {
  return (
    <div className={`flex gap-1 w-full min-w-0 items-stretch ${className}`}>
      {options.map(({ key, label, icon: Icon }) => {
        const isActive = activeFilter === key;
        return (
          <button
            key={key}
            type="button"
            onClick={() => onFilterChange(key)}
            className={`transition-all duration-200 min-w-0 rounded-lg h-full flex items-center justify-center
              ${isActive 
                ? 'flex-[2] bg-blue-600 text-white shadow-md gap-3 px-4 py-3'
                : 'flex-1 bg-gray-100 text-gray-700 hover:bg-gray-200 px-0 py-3'
              }
            `}
            aria-pressed={isActive}
            style={{ minWidth: 0 }}
          >
            <Icon className="h-5 w-5 flex-shrink-0" />
            {isActive && (
              <span className="text-base font-semibold truncate text-center leading-tight ml-2">
              {label}
            </span>
            )}
          </button>
        );
      })}
    </div>
  );
};
